				"html": "<p>なぜ生命は連続性を好むのか。<br>\n私の存在は泡のように消え<br>\nまた再び現れるということはない。  </p>\n<p>たとえそれを望んだとしても。  </p>\n<p>もし，存在がデジタルの世界に置き換えられるとしたら<br>\nあるときの状態は情報として保存され<br>\nまた別の任意の時間に復元することができるのかもしれない。  </p>\n<p>しかし，それは生命ではない別の何かだ。  </p>\n<p>不変ではなく，常に変化し続け<br>\nしかも途絶えることがない。<br>\nいつか一個体としての生命として<br>\n終わりを迎えるまでは。  </p>\n<p>変化し続ける私は私ではあるが<br>\nその瞬間の私と次の瞬間の私は別のものであるとも言える。<br>\nそして，そのようであることが連続している。<br>\nそのような存在としての私が<br>\n生命としての私である。  </p>\n<p>生命は身体に宿り，身体は変化し続ける。<br>\n意識や精神も身体の機能の一部であるならば<br>\nそれもまた生命の一部である。  </p>\n<p>もし誰かが彼と話をしたら<br>\nそれは単に彼から情報が引き出された<br>\nというだけではなく<br>\n同時に彼の中の変化に影響を与えている。  </p>\n<p>対話や思考はそれ自体によって変化がもたらされる。<br>\nデータベースから情報を取り出すこととは異なるものだ。  </p>\n<p>それは保存されることのない情報が<br>\n止まることのない時間の中で流れ続けているともいえる。<br>\nそれは完全に捉えることができず<br>\n再現することはできない。<br>\nやり直すことはできない。  </p>\n<p>・・・</p>\n<p>しかしながら，意識に関して言えば<br>\nデジタル化によって情報として保存することができる可能性がある<br>\nという話もある。  </p>\n<p>もし再現された自我が異なる環境要因で変化するならば<br>\nオリジナルとは違った変化をするだろう。<br>\nすると異なった分岐の可能性は永遠と続き<br>\n並行する異なる意識が無限に増える。<br>\nそうだとしても，それらは私と言えるのだろうか。  </p>\n<p>あるいは，環境要因で変化しないのであれば<br>\nそれは単なる情報であって<br>\nある側面の写像といえるかもしれないが<br>\n生命とは言えない。  </p>\n<p>また，デジタル化された情報であれば<br>\nその一部を書き換えることもできだろう。<br>\nそうした場合，それは人のものといえるのか，<br>\nそれとも人工的なものというべきものなのか。  </p>\n<p>身体についても<br>\n組成情報の保存と復元方法が確立されたとすれば<br>\n再現できるのかもしれない。<br>\n仮に機能が同じであっても，それは生命といえるのだろうか。<br>\nロボットとの違いはあるのか。  </p>\n<p>たとえ，連続性を排除することができたとしても，<br>\nその存在は生命であり続けられるのであろうか。  </p>",
